#globe {
    position: absolute;

    width: 80vw;

    bottom: -60vw;

    border-radius: 1100px;
}


.Star {
    border-radius: 100%;
    position: absolute;
    bottom: 0px;
    right: 0px;
}

.StarWhite {
    background-color: white;
}

.StarBlue {
    background-color:#003b59;
}

.StarPurple {
    background-color: #682860;
}

.StarYellow {
    background-color: yellow;
}

.starArea {
    position: absolute;
    padding: 25px ;
    background-color: transparent;
}

.jumbotron {
    position: relative;
    display: block;

    text-align: center;

    background-color: transparent;
    
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;

    z-index: 0;

    margin-top: 10%;
    margin-bottom: 30%;

    padding-top: 10px !important;
    padding-bottom: 10px !important;

    border-radius: 50px;

}

.jumbotron h1 {
    display: inline;
    font-size: 50px;
    color: white;
    font-family: 'Raleway', sans-serif;
}

.row {
    cursor: pointer;
}

.jumbotron img {
    position: relative;
    margin-top: 50px;
    z-index: 1000;
    cursor: pointer;
    width: 60px;
    -webkit-transition: width .2s;
    transition: width .2s;

}

.jumbotron img:hover {
    width: 100px;
}

.ShootingStar {
    position: absolute;
    width: 1px;
    height: 1px;
    background-color: white;
}
.Space {
    width: 100%;
    height: 100%;
}
.scoreboard {
    padding: 20px;
    color: White;
    z-index: 1000;
    display: inline;
}
html, body {
    overflow: hidden;
    background-color: black;
    cursor: url(data:image/png;base64,), auto;
}

.App {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
}

